@media (min-width: 1024px) and (max-width: 1439px) {
  .headerWrapper {
    width: 100vw;
    height: 90px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.12);
    font-family: "Inter UI";
    position: fixed;
    z-index: 100;
    background-color: #fff; }
    .headerWrapper .header {
      width: 924px;
      margin: 0 auto;
      padding-top: 17px;
      display: flex;
      justify-content: space-between; }
      .headerWrapper .header .headerLogo {
        display: block;
        width: 120px;
        height: 40px;
        padding-top: 6px; }
      .headerWrapper .header .headerMenuWrapper {
        height: 51px;
        display: flex;
        justify-content: space-between;
        align-items: center; }
        .headerWrapper .header .headerMenuWrapper .headerMenu {
          width: 454px;
          display: flex;
          justify-content: space-between;
          padding-right: 34px; }
          .headerWrapper .header .headerMenuWrapper .headerMenu a {
            font-weight: 600;
            font-size: 19px;
            line-height: 23px;
            text-decoration: none;
            color: black; }
          .headerWrapper .header .headerMenuWrapper .headerMenu .headerMenuActive {
            position: relative;
            height: 100%; }
            .headerWrapper .header .headerMenuWrapper .headerMenu .headerMenuActive::after {
              content: '';
              position: absolute;
              display: block;
              width: 100%;
              height: 6px;
              top: 53px;
              background-color: #4F261C; }
        .headerWrapper .header .headerMenuWrapper .headerCallWrapper {
          display: block;
          width: 234px;
          height: 51px;
          background: #FFC700;
          box-shadow: 4px 4px 22px rgba(98, 48, 36, 0.16);
          border-radius: 20px;
          display: flex;
          justify-content: center;
          align-content: center;
          text-decoration: none; }
          .headerWrapper .header .headerMenuWrapper .headerCallWrapper img {
            padding-right: 7px; }
          .headerWrapper .header .headerMenuWrapper .headerCallWrapper span {
            display: flex;
            align-self: center;
            font-size: 18px;
            line-height: 22px;
            font-weight: 700;
            color: black;
            text-decoration: none; } }

