      .cardWrapper .cardControls div {
        width: 50px;
        height: 50px; }
        .cardWrapper .cardControls div img {
          width: 50px;
          height: 50px; }
      .cardWrapper .cardControls a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        border-radius: 50px;
        transition: 0.2s; }
        .cardWrapper .cardControls a:hover {
          transition: 0.2s;
          box-shadow: 0px 5px 22px rgba(0, 0, 0, 0.15); }
    .cardWrapper .cardContent {
