.taxesCard {
  position: relative;
  width: 33%;
  height: 254px;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  padding: 23px 17px 18px 17px;
  box-sizing: border-box;
  font-family: "Inter UI";
  transition: 0.5s;
  text-decoration: none; }
  .taxesCard .illustration {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    position: absolute;
    max-width: 55px; }
  .taxesCard .taxesCardTagsWrapper {
    display: flex;
    justify-content: space-between; }
    .taxesCard .taxesCardTagsWrapper .taxesCardTag {
      display: flex;
      align-items: center;
      font-size: 16px; }
      .taxesCard .taxesCardTagsWrapper .taxesCardTag img {
        display: block; }
      .taxesCard .taxesCardTagsWrapper .taxesCardTag span {
        text-decoration: none;
        color: black;
        margin-left: 7px;
        font-weight: 500; }
  .taxesCard h2 {
    text-align: center;
    font-size: 19px;
    line-height: 25px;
    text-transform: uppercase;
    text-decoration: none;
    color: black; }
  .taxesCard:nth-child(3n+0) {
    border-bottom: 1px solid #9A8974;
    border-left: 1px solid #9A8974; }
  .taxesCard:nth-child(3n+1) {
    border-bottom: 1px solid #9A8974;
    border-right: 1px solid #9A8974; }
  .taxesCard:nth-child(3n+2) {
    border-bottom: 1px solid #9A8974; }
  .taxesCard:nth-last-child(-n+3) {
    border-bottom: 0px; }
  .taxesCard:hover {
    transition: 0.5s;
    background-color: #fff;
    box-shadow: 0px 5px 22px rgba(0, 0, 0, 0.25); }
