.serviceRow {
  display: flex;
  align-items: center;
  width: 100%;
  height: 104px;
  font-family: "Inter UI";
  justify-content: space-between;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
  .serviceRow span {
    font-weight: 500;
    font-size: 28px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.9); }
  .serviceRow a {
    color: black;
    text-decoration: none;
    font-size: 20px;
    width: 124px;
    height: 46px;
    display: flex;
    justify-content: center;
    padding: 13px 0 0;
    box-sizing: border-box;
    background: #FFFFFF;
    box-shadow: 0px 5px 22px rgba(0, 0, 0, 0.25);
    border-radius: 13px;
    transition: 0.2s; }
    .serviceRow a:hover {
      transition: 0.2s;
      box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.25); }
